/* 主容器，确保内容左右居中对齐，背景色为浅灰色，占满视口高度 */
.update-prop-container {
    display: flex; /* 使用Flexbox布局 */
    justify-content: center; /* 水平居中对齐 */
    padding: 20px; /* 内边距 */
    background-color: #f4f4f4; /* 背景颜色 */
    min-height: 100vh; /* 最小高度设置为视口高度 */
}

/* 内容容器，包含标题和表单，宽度自适应并最大不超过700px */
.update-prop-content {
    width: 100%; /* 宽度占满父容器 */
    max-width: 700px; /* 最大宽度为700px */
    text-align: left; /* 文本左对齐 */
}

/* 表单样式，确保宽度占满父容器并有顶部间距 */
form {
    width: 100%; /* 表单宽度占满父容器 */
    margin-top: 20px; /* 表单顶部间距 */
}

/* 表单组样式，每个表单项底部有间距 */
.form-group {
    margin-bottom: 15px; /* 每个表单组底部间距 */
}

/* 标签样式，块级显示并底部有间距 */
label {
    display: block; /* 块级显示 */
    margin-bottom: 5px; /* 标签底部间距 */
}

/* 输入框和选择框样式，宽度占满父容器并有内边距和边框 */
input[type="text"], select {
    width: 100%; /* 宽度占满父容器 */
    padding: 10px; /* 内边距 */
    margin-bottom: 10px; /* 底部间距 */
    border: 1px solid #ccc; /* 边框 */
    border-radius: 4px; /* 圆角边框 */
}

/* 单选框组样式，水平排列并居中对齐 */
.radio-group {
    display: flex; /* 使用Flexbox布局 */
    align-items: center; /* 垂直居中对齐 */
    justify-content: center; /* 水平居中对齐 */
}

/* 单选框标签样式，右侧有间距 */
.radio-label {
    margin-right: 20px; /* 右侧间距 */
}

/* 提交按钮样式，背景色、内边距、边框和圆角 */
input[type="submit"] {
    background: #007bff; /* 背景颜色 */
    color: white; /* 字体颜色 */
    padding: 10px 15px; /* 内边距 */
    border: none; /* 无边框 */
    border-radius: 4px; /* 圆角边框 */
    cursor: pointer; /* 鼠标指针 */
    width: 100%; /* 宽度占满父容器 */
}

/* 提交按钮悬停样式，改变背景色 */
input[type="submit"]:hover {
    background: #0056b3; /* 背景颜色 */
}
