/* 侧边栏的主容器 */
.sidebar {
    width: 200px; /* 设置侧边栏的宽度 */
    background-color: #f5f5f5; /* 设置侧边栏的背景颜色 */
    border-right: 1px solid #ddd; /* 给侧边栏添加一个右边框 */
    box-shadow: 2px 0 5px rgba(211, 18, 18, 0.1); /* 给侧边栏添加一个轻微的阴影 */
    padding: 20px; /* 给侧边栏内容添加一些内边距 */
    height: 100vh; /* 设置侧边栏的高度为视口高度 */
    overflow-y: auto; /* 如果内容超出高度,添加垂直滚动条 */
}

/* 设置侧边栏内部的无序列表样式 */
.sidebar ul {
    list-style-type: none; /* 移除默认的项目符号 */
    padding: 0; /* 移除默认的内边距 */
    margin: 0; /* 移除默认的外边距 */
}

/* 设置侧边栏内部列表项的样式 */
.sidebar li {
    margin-bottom: 10px; /* 给列表项之间添加一些间距 */
}

/* 设置侧边栏内部链接容器的样式 */
.sidebar .link-container {
    display: flex; /* 使用 flexbox 来排列链接和切换按钮 */
    align-items: center; /* 垂直居中内部内容 */
    justify-content: space-between; /* 水平方向上分散排列内部内容 */
    padding: 8px 12px; /* 给链接容器添加一些内边距 */
    border-radius: 4px; /* 给链接容器添加一些圆角 */
    transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease; /* 添加一些平滑的过渡动画 */
    cursor: pointer;
}

/* 设置鼠标悬浮或展开状态下链接容器的样式 */
.sidebar .link-container:hover,
.sidebar .link-container.expanded {
    background-color: #e0e0e0; /* 在悬浮或展开状态下改变背景颜色 */
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1); /* 添加一些轻微的阴影 */
    transform: translateX(2px); /* 向右移动链接容器一点点 */
}

/* 设置链接容器内部链接的样式 */
.sidebar .link-container a {
    color: #333; /* 设置链接的文字颜色 */
    text-decoration: none; /* 移除默认的下划线 */
    font-size: 14px; /* 设置链接的字体大小 */
    font-weight: 500; /* 设置链接的字体粗细 */
    display: block; /* 将链接设置为块级元素,占据整个容器 */
    padding: 8px 12px; /* 给链接添加一些内边距 */
    border-radius: 4px; /* 给链接添加一些圆角 */
    transition: background-color 0.3s ease; /* 添加背景色过渡动画 */
}

/* 设置当前激活链接的样式 */
.sidebar .link-container a.active {
    color: #007bff; /* 设置当前激活链接的文字颜色 */
    background-color: #e0e0e0; /* 设置当前激活链接的背景色 */
}

/* 设置链接容器内部切换按钮的样式 */
.sidebar .toggle-button {
    background-color: transparent; /* 移除默认的按钮背景色 */
    border: none; /* 移除默认的按钮边框 */
    color: #333; /* 设置切换按钮的文字颜色 */
    font-size: 16px; /* 设置切换按钮的字体大小 */
    cursor: pointer; /* 在悬浮时显示指针光标 */
}

/* 设置侧边栏内部子菜单的样式 */
.sidebar .submenu {
    margin-left: 20px; /* 给子菜单添加一些左边距 */
    max-height: 0; /* 设置子菜单的初始最大高度为 0 */
    overflow: hidden; /* 隐藏子菜单的溢出部分 */
    transition: max-height 0.3s ease; /* 添加一些平滑的过渡动画 */
}

/* 设置展开状态下子菜单的样式 */
.sidebar .submenu.expanded {
    max-height: 1000px; /* 设置子菜单的最大高度为一个较大的值,以显示完整的子菜单 */
}

/* 针对较小屏幕的媒体查询 (最大宽度: 768px) */
@media (max-width: 768px) {
    /* 设置小屏幕下侧边栏的样式 */
    .sidebar {
        position: fixed; /* 将侧边栏设置为固定位置 */
        top: 0; /* 将侧边栏定位在视口顶部 */
        left: -250px; /* 将侧边栏移动到视口外部 */
        width: 250px; /* 设置侧边栏的宽度 */
        height: 100%; /* 设置侧边栏的高度为视口高度 */
        transition: left 0.3s ease; /* 添加侧边栏左侧位置的平滑过渡 */
        z-index: 1000; /* 设置较高的 z-index 确保侧边栏位于顶层 */
    }

    /* 设置展开状态下侧边栏的样式 */
    .sidebar.expanded {
        left: 0; /* 将侧边栏移动到视口中 */
    }

    /* 设置小屏幕下侧边栏遮罩层的样式 */
    .sidebar-overlay {
        position: fixed; /* 将遮罩层设置为固定位置 */
        top: 0; /* 将遮罩层定位在视口顶部 */
        left: 0; /* 将遮罩层定位在视口左侧 */
        width: 100%; /* 设置遮罩层的宽度为视口宽度 */
        height: 100%; /* 设置遮罩层的高度为视口高度 */
        background-color: rgba(0, 0, 0, 0.5); /* 设置遮罩层的背景颜色 */
        z-index: 999; /* 设置较高的 z-index 确保遮罩层位于侧边栏下方 */
        display: none; /* 初始时隐藏遮罩层 */
    }

    /* 设置展开状态下遮罩层的样式 */
    .sidebar-overlay.show {
        display: block; /* 当侧边栏展开时显示遮罩层 */
    }
}