.App {
    font-family: Arial, sans-serif;
}

.container {
    display: flex;
}

.main-content {
    flex: 1;
    padding: 1rem;
}

a {
    color: #0077b6; /* 蓝色链接,符合常见web标准 */
    text-decoration: none; /* 去掉默认下划线 */
}

a:hover {
    color: #005a8e; /* 鼠标悬停时稍微变深 */
    text-decoration: underline; /* 添加下划线 */
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}