.main-content {
    flex: 1;
    padding: 1rem;
}


/* 表单样式 */
form {
    margin: 40px auto;
    width: 90%;
    max-width: 500px;
}

label {
    display: block;
    margin-bottom: 10px;
}

input[type="text"],
select {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
    box-sizing: border-box;
}

input[type="submit"] {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
}

.form-group {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.form-group label {
    margin-right: 10px;
    width: 150px;
    text-align: right;
}

.form-group input[type="text"],
.form-group select {
    flex: 1;
}

.form-group.radio-group label {
    margin-right: 5px;
}

.form-group.radio-group input[type="radio"] {
    margin-right: 3px;
}

/* 输入框必填标记样式 */

.form-group input[required]::placeholder {
    color: #A15757FF;
}

.form-group input[required] + label::after {
    content: "*";
    color: #a15757;
    margin-left: 5px;
}

/* 响应式布局 */
@media (max-width: 767px) {
    .form-group {
        flex-direction: column;
        align-items: flex-start;
    }

    .form-group label {
        width: auto;
        text-align: left;
        margin-bottom: 5px;
    }

    .form-group input[type="text"],
    .form-group select {
        width: 100%;
    }
}


